const markdown = `
* secure shell (SSH) = cryptographic network protocol for operating network services securely over an unsecured network.
* typical applications include remote command-line, login, and remote command execution, but any network service can be secured with SSH.
* SSH is typically used to log into a remote machine and execute commands
  * but it also supports tunneling, forwarding TCP ports and X11 connections; 
  * it can transfer files using the associated SSH file transfer (SFTP) or secure copy (SCP) protocols.
* SSH uses the client-server model.
`

export default markdown