const markdown = `
* offer data storage and retrieval that is modelled differently to "traditional" relational databases. 
* typically focus more on horizontal scaling, eventual consistency, speed and flexibility and is used commonly for big data and real-time streaming applications. 
* NoSQL is often described as a BASE system (Basically Available, Soft state, Eventual consistency) 
  * as opposed to SQL/relational which typically focus on ACID (Atomicity, Consistency, Isolation, Durability). 
* common NoSQL data structures include key-value pair, wide column, graph and document.
`

export default markdown